.goods {
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: 10px 0;
    padding: 5px;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid rgb(255, 255, 91);
    border-radius: 2px;
    list-style-type: none;
  }

  &__clear {
    position: absolute;
    top: 50%;
    right: 20%;
    display: inline-block;
    margin: 0 20px;
    font-size: 30px;
    transform: translateY(-50%);
  }

  &__header-wrapper {
    position: relative;
  }
}
